// 
export function uploadImages(req) {
	return uni.uploadFile({
		url: baseUrl + '/product/image/upload',
		filePath: req.filePath,//,successres.tempFilePaths
		name: 'file',
		header: {
			"X-USER-ID": getApp().globalData.USER_ID
		}
	})
}


// 
export function publishProduct(req) { 
	return uni.request({
		url: baseUrl + '/product/publish',
		method: "POST",
		header:{
			"X-USER-ID": getApp().globalData.USER_ID
		},
		data: {
			id: req.id == null ? null : req.id,
			userId: req.userId,
			title: req.title,
			content: req.content,
			tags: req.tags,
			price: req.price,
			imageJson: req.imageJson,
			setId:req.setId
		}
	});
}


