export function changeProductStatus(index) {
	if (index == 'REVIEW') {
		return ""
	} else if (index == 'REJECT') {
		return ""
	} else if (index == 'SUCCESS') {
		return ""
	}
}

export function changeCity(index) {
	if (index == '0') {
		return ""
	}
	if (index == '1') {
		return ""
	}
	if (index == '2') {
		return ""
	}
	if (index == '3') {
		return ""
	} else {
		return ""
	}
}

export function changeTag(tagstr) {
	let tagList = tagstr.split(',');
	let res = [];
	tagList.forEach(index => {
		if (index == 0) {
			res.push("")
		} else if (index == 1) {
			res.push("")
		} else if (index == 2) {
			res.push("")
		} else if (index == 3) {
			res.push("")
		} else if (index == 4) {
			res.push("")
		} else if (index == 5) {
			res.push("")
		} else if (index == 6) {
			res.push("")
		}
	})

	return res;
}

export function changePeopleNum(index) {
	if (index === 0) {
		return ''
	}
	if (index === 1) {
		return ''
	}
	if (index === 2) {
		return ''
	}
}

